import { useState } from "react";

// @mui material components
// import Link from "@mui/material/Link";
import "./Modal.css";
// import Icon from "@mui/material/Icon";
import Grid from "@mui/material/Grid";
import "./footerr.css";
import { FaAddressCard } from "@react-icons/all-files/fa/FaAddressCard";

// import { FaQrcode } from "@react-icons/all-files/fa/FaQrcode";
import { FaFileInvoice } from "@react-icons/all-files/fa/FaFileInvoice";
import Card from "@mui/material/Card";
// import Button from "@mui/material/Button";
// import burceMar1 from "assets/images/qrkodu.svg";

// Soft UI Dashboard React components
import SuiBox from "components/SuiBox";
import SuiTypography from "components/SuiTypography";

import { Button } from "@mui/material";

// Soft UI Dashboard React base styles
// import typography from "assets/theme/base/typography";

function Footer() {
  const [modal1, setModal] = useState(false);
  const [modal4, setModal4] = useState(false);

  const toggleModal = () => {
    setModal(!modal1);
  };

  const toggleModal4 = () => {
    setModal4(!modal4);
  };

  return (
    <SuiBox
      width="100%"
      display="flex"
      flexDirection={{ xs: "column", lg: "row" }}
      justifyContent="space-between"
      alignItems="center"
      px={1.5}
      sx={{
        backdropFilter: `saturate(200%) blur(30px)`,
        backgroundColor: ({ functions: { rgba }, palette: { white } }) => rgba(white.main, 0.8),
        boxShadow: ({ boxShadows: { navbarBoxShadow } }) => navbarBoxShadow,
      }}
    >
      <Grid container pb={2}>
        <Grid item xs={6} md={6} xl={6} onClick={toggleModal} sx={{ textAlign: "center" }}>
          <SuiTypography variant="button" fontWeight="small">
            <FaAddressCard />
            <div>Banka</div>
          </SuiTypography>
        </Grid>
        {modal1 && (
          <Card
            className="toTheTop"
            sx={{
              backdropFilter: `saturate(200%) blur(30px)`,
              backgroundColor: ({ functions: { rgba }, palette: { white } }) =>
                rgba(white.main, 0.8),
              boxShadow: ({ boxShadows: { navbarBoxShadow } }) => navbarBoxShadow,
              position: "relative",
              mt: -8,
              mx: 3,
              py: 2,
              px: 2,
            }}
          >
            <Grid container spacing={3} alignItems="center">
              <Grid item xs={12}>
                <h3 style={{ textAlign: "center" }}>Banka Bilgileri</h3>
                <hr style={{ color: "black" }} />
              </Grid>

              <Grid item xs={12}>
                <SuiBox height="100%" lineHeight={1}>
                  <SuiTypography variant="h6" fontWeight="medium">
                    Vakıfbank
                  </SuiTypography>
                </SuiBox>
              </Grid>
              <Grid item xs={12}>
                Ahmet Burak Taşkıran.
              </Grid>
              <Grid item> TR 10 0001 5001 58007 2928 09350</Grid>

              <Grid item xs={12} style={{ textAlign: "center" }}>
                <Button style={{ background: "black" }} fullWidth>
                  {" "}
                  IBAN KOPYALA{" "}
                </Button>
              </Grid>
            </Grid>
            <Button onClick={toggleModal}>X</Button>
          </Card>
        )}

        <Grid item xs={6} md={6} xl={6} onClick={toggleModal4} sx={{ textAlign: "center" }}>
          <SuiTypography variant="button" fontWeight="small">
            <FaFileInvoice />
            <div>&nbsp;Fatura&nbsp;</div>
          </SuiTypography>
        </Grid>
        {modal4 && (
          <Card
            className="toTheTop"
            sx={{
              backdropFilter: `saturate(200%) blur(30px)`,
              backgroundColor: ({ functions: { rgba }, palette: { white } }) =>
                rgba(white.main, 0.8),
              boxShadow: ({ boxShadows: { navbarBoxShadow } }) => navbarBoxShadow,
              position: "relative",
              mt: -8,
              mx: 3,
              py: 2,
              px: 2,
            }}
          >
            <Grid container spacing={3} alignItems="center">
              <Grid item xs={12}>
                <h3 style={{ textAlign: "center" }}>Fatura Bilgileri</h3>
                <hr style={{ color: "black" }} />
              </Grid>

              <Grid item xs={12}>
                <SuiBox height="100%" lineHeight={1}>
                  <SuiTypography variant="body2" fontWeight="medium">
                    Av. Ahmet Burak TAŞKIRAN
                  </SuiTypography>
                </SuiBox>
              </Grid>
              <Grid item xs={12}>
                <h6>Muhsin Yazıcıoğlu Cad No:10/3 Çukurambar-Çankaya/ANKARA</h6>
              </Grid>
              <Grid item>
                <h6>Vergi No: 10864320824</h6>
              </Grid>

              <Grid item xs={12} style={{ textAlign: "center" }}>
                <Button style={{ background: "black" }} fullWidth>
                  {" "}
                  Fatura Bilgilerini Kopyala{" "}
                </Button>
              </Grid>
            </Grid>
            <Button onClick={toggleModal4}>X</Button>
          </Card>
        )}
      </Grid>
    </SuiBox>
  );
}

