import React, { useState, useEffect } from "react";

// @mui material components
import Card from "@mui/material/Card";
import Grid from "@mui/material/Grid";
import AppBar from "@mui/material/AppBar";
import Tabs from "@mui/material/Tabs";
// import Tab from "@mui/material/Tab";

// Soft UI Dashboard React components
import SuiBox from "components/SuiBox";
import SuiTypography from "components/SuiTypography";
import SuiAvatar from "components/SuiAvatar";
import { FaQrcode } from "@react-icons/all-files/fa/FaQrcode";
import "./HeaderStyle.css";

// Soft UI Dashboard React examples
// import DashboardNavbar from "examples/Navbars/DashboardNavbar";

// Soft UI Dashboard React icons
// import Cube from "examples/Icons/Cube";
// import Document from "examples/Icons/Document";
// import Settings from "examples/Icons/Settings";

// toastify
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

// Soft UI Dashboard React base styles
import breakpoints from "assets/theme/base/breakpoints";

// Images
import burceMars from "assets/images/samet.jpg";
import curved0 from "assets/images/curved-images/hibrit1.jpeg";
import { FaShareAlt } from "@react-icons/all-files/fa/FaShareAlt";

function Header() {
  const [tabsOrientation, setTabsOrientation] = useState("horizontal");
  const [tabValue, setTabValue] = useState(0);

  const notify = () =>
    toast("Sayfa Linki Kopyalandı ", {
      position: "top-center",
      autoClose: 2000,
      hideProgressBar: true,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      theme: "dark",
      progress: undefined,
    });

  useEffect(() => {
    // A function that sets the orientation state of the tabs.
    function handleTabsOrientation() {
      return window.innerWidth < breakpoints.values.sm
        ? setTabsOrientation("vertical")
        : setTabsOrientation("horizontal");
    }

    /** 
     The event listener that's calling the handleTabsOrientation function when resizing the window.
    */
    window.addEventListener("resize", handleTabsOrientation);

    // Call the handleTabsOrientation function to set the state with the initial value.
    handleTabsOrientation();

    // Remove event listener on cleanup
    return () => window.removeEventListener("resize", handleTabsOrientation);
  }, [tabsOrientation]);

  const handleSetTabValue = (event, newValue) => setTabValue(newValue);

  return (
    <SuiBox position="relative">
      {/* <DashboardNavbar absolute light /> */}
      <SuiBox
        display="flex"
        alignItems="center"
        position="relative"
        minHeight="11.75rem"
        sx={{
          backgroundImage: () => ` url(${curved0})`,
          backgroundSize: "cover",
          backgroundPosition: "50%",
          overflow: "hidden",
          borderRadiusBottom: 5,
        }}
      />
      <Card
        sx={{
          backdropFilter: `saturate(200%) blur(30px)`,
          backgroundColor: ({ functions: { rgba }, palette: { white } }) => rgba(white.main, 0.8),
          boxShadow: ({ boxShadows: { navbarBoxShadow } }) => navbarBoxShadow,
          position: "relative",
          mt: -8,
          mx: 3,
          py: 2,
          px: 2,
        }}
      >
        <Grid container spacing={3} alignItems="center">
          <Grid item xs={12} sx={{ display: "flex" }}>
            <Grid xs={6}>
              <SuiAvatar
                src={burceMars}
                alt="profile-image"
                variant="rounded"
                size="xl"
                shadow="sm"
              />
            </Grid>
            <Grid xs={6}>
              <SuiTypography variant="button" fontWeight="small" sx={{ float: "right" }}>
                <FaQrcode />
                &nbsp;&nbsp; &nbsp;&nbsp;
                <FaShareAlt
                  onClick={() => {
                    navigator.clipboard.writeText("hibritcard.com/id/abtaskiran");
                    notify();
                  }}
                />
              </SuiTypography>
            </Grid>
          </Grid>
          <Grid item>
            <SuiBox height="100%" lineHeight={1}>
              <SuiTypography variant="h5" fontWeight="medium">
                Samet Çevik
              </SuiTypography>
              <SuiTypography variant="button" color="text" fontWeight="medium">
                Hibrit Medya A.Ş.&nbsp;
              </SuiTypography>
              <SuiTypography variant="button" color="text" fontWeight="medium">
                / Genel Müdür.
              </SuiTypography>
            </SuiBox>
          </Grid>
          <Grid item xs={12} md={6} lg={4} sx={{ ml: "auto" }}>
            <AppBar position="static">
              <Tabs
                orientation={tabsOrientation}
                value={tabValue}
                onChange={handleSetTabValue}
                sx={{ background: "transparent" }}
              >
                {/* <Tab label="App" icon={<Cube />} />
          <Tab label="Message" icon={<Document />} />
          <Tab label="Settings" icon={<Settings />} /> */}
              </Tabs>
            </AppBar>
          </Grid>
        </Grid>
      </Card>
      <ToastContainer />
    </SuiBox>
  );
}

export default Header;
